import React from "react";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Doughnut } from 'react-chartjs-2';


ChartJS.register(ArcElement, Tooltip, Legend);
ChartJS.defaults.font.size = 14;
ChartJS.defaults.color = 'rgb(21, 67, 96)';
ChartJS.defaults.plugins.legend.position = 'bottom';
ChartJS.defaults.plugins.legend.labels.boxWidth=20;
ChartJS.defaults.plugins.legend.labels.boxHeight=20;
ChartJS.defaults.plugins.legend.labels.padding = 10;


export default function Graph(props) {
  const data = {
    labels: props.label,
    datasets: [
      {
        label: 'млрд руб',
        data: props.data,
        backgroundColor: [
          'rgb(0,191,255)',
          'rgb(30,144,255)',
          'rgb(65,105,225)',
          'rgb(0,0,255)',
          'rgb(0,0,205)',
          'rgb(0,191,255)',
        ],
        borderColor: [
          'rgb(0,191,255)',
          'rgb(30,144,255)',
          'rgb(65,105,225)',
          'rgb(0,0,255)',
          'rgb(0,0,205)',
          'rgb(0,191,255)',
        ],
        borderWidth: 1,
        positionL: 'bottom'
      },
    ],
  };
    return (        
        <div className="graph">
            <Doughnut data={ data } go />
        </div>
    )
}