import React from "react";

export default function ContactElement(props) {
    let style = "jumbltorn " + props.bg;

    return (
        <div className={ style }>
            <h1 className="display-3">{ props.name }</h1>
            <p className="lead">{ props.todo }</p>
            <hr className="my-2" />
            <p>{ props.info }</p>
            <p className="lead">
            <a href={ props.link } className="text-main bord1">Узнать больше</a>
            </p>
        </div>
    )
}