import React from "react";


export default function RightText(props) {
    let style = "rightText " + props.bg;

    return (
        <div className={ style }>
            <div>
                <img src={ props.svg } width={300} height={300} alt=''></img>
            </div>
            <div>
                <h1>{props.title}</h1>
                <p>{props.text}</p>
            </div>
        </div>
    )
}
