import React from "react";

export default function LeftText(props) {
    let style = "leftText " + props.bg;

    return (
        <div className={ style }>
            <div>
                <h1>{ props.title}</h1>
                <p>{ props.text }</p>
            </div>
            <div>
                <img src={ props.svg } width={400} height={400} alt=''></img>
            </div>
        </div>
    )
}

