import 'bootstrap/dist/css/bootstrap.min.css';
import React, { Component } from 'react';
import { Navbar, Nav, Offcanvas} from 'react-bootstrap';
import { NavLink} from 'reactstrap';
import { Link } from 'react-router-dom';

export default class NavBar extends Component {
  constructor (props) {
    super(props);

      this.toggleNavbar = this.toggleNavbar.bind(this);

      this.state = {
        collapsed: true,
      };


      window.addEventListener('scroll', function () {
          var minOffset = 965;
          let has_class = document.body.classList.contains("scroll_navbar");
          if (minOffset < window.scrollY) {
              document.body.classList.add('scroll_navbar');
          }
          else if (has_class) {
              document.body.classList.remove('scroll_navbar');
          }
      });
    }


    toggleNavbar () {
        this.setState({
            collapsed: !this.state.collapsed
        });
    }
    
    home () {
        // window.location.href("/");
    };
    direction () {
        // window.location.href("/Direction");
    };
    contact () {
        // window.location.href("/Contact");
    };

    render() {
            return (
                <header className='navbar-outer navbar-expand-lg fixed-top'>
                <Navbar expand="sm" className='container-fluid navbar-inner'>
                    <div className='container'>
                    <Navbar.Brand className='navbar-brand' href="/BudgetMos/">Бюджет <br/> для <br/> граждан</Navbar.Brand>
                    <Navbar.Toggle aria-controls={`offcanvasNavbar-expand-sm`} onClick={this.toggleNavbar} />
                    <Navbar.Offcanvas id={`offcanvasNavbar-expand-sm`} aria-labelledby={`offcanvasNavbarLabel-expand-sm`} show={this.state.collapsed} placement="end">
                        <div className='offcanvas-item'>
                        <Offcanvas.Header closeButton>
                            <Offcanvas.Title id={`offcanvasNavbarLabel-expand-sm`} className="text-title">
                            <h1 className='text-main'>Бюджет для граждан</h1>
                            </Offcanvas.Title>
                        </Offcanvas.Header>
                        <Offcanvas.Body>
                            <Nav className="flex-grow-1 pe-3">
                                <NavLink tag={ Link } className = "text-main" to="BudgetMos/" onClick={this.toggleNavbar}>Бюджет</NavLink>
                                <NavLink tag={ Link } className = "text-main" to="BudgetMos/Direction" onClick={this.toggleNavbar}>Направления</NavLink>
                            </Nav>
                            <Nav className="flex-grow-2 pe-3">
                                <NavLink tag={ Link } className = "text-main" to="BudgetMos/Contact" onClick={this.toggleNavbar} aria-controls={`offcanvasNavbar-expand-sm`} >Контакты</NavLink>
                            </Nav>
                        </Offcanvas.Body>
                        </div>
                    </Navbar.Offcanvas> 
                    </div>
                </Navbar>
                </header>
            );
        }
}
