import Main from './pages/Main';
import Direction from './pages/Direction';
import Contact from './pages/Contact';


const AppRoutes = [
    {
        path: 'BudgetMos/',
        element: <Main />
    },
    {
        path: 'BudgetMos/Direction',
        element: <Direction />
    },
    {
        path: 'BudgetMos/Contact',
        element: <Contact />
    }
];

export default AppRoutes;