import React from 'react';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { Bar } from 'react-chartjs-2';






export default function DirElement(props) {
  ChartJS.register(
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend
  );

  const data = {
    labels: " ",
    datasets: [
      {
        data: [props.value, 1000],
        backgroundColor: ['blue'],
        barThickness: 25,
        borderRadius: 50, 
      }
    ],
  };

  const options = {
    indexAxis: 'y',
    cornerRadius: 20,
    plugins: {
      legend: {
        display: false,
      },
      title: {
        display: false,
      },
    },
  };

  return (
    <div className='dirElement'>
      <a href={props.href}>{props.name}</a>
      <Bar className='bar' options={options} data={data}/>
    </div>
  )
}
 