import LeftText from '../component/LeftText.jsx';
import RightText from '../component/RightText.jsx';
import TextGraph from '../component/TextGraph';
import Caption from '../component/Caption';
import Carousel from '../component/Carousel';

import budget from '../illustration/budget.svg';
import plan from '../illustration/plan.svg';

function Main() {
  return (
    <> 
      <Caption title="БЮДЖЕТ" text="форма образования и расходования денежных средств, предназначенных для финансового обеспечения задач и функций государства и местного самоуправления."/>
      <LeftText bg="blue" svg={budget} title="БЮДЖЕТНАЯ КЛАССИФИКАЦИЯ" text="Бюджетная классификация Российской Федерации является группировкой доходов, расходов и источников финансирования дефицитов бюджетов бюджетной системы Российской Федерации, используемой для составления и исполнения бюджетов, а также группировкой доходов, расходов и источников финансирования дефицитов бюджетов и (или) операций сектора государственного управления, используемой для ведения бюджетного (бухгалтерского) учета, составления бюджетной (бухгалтерской) и иной финансовой отчетности, обеспечивающей сопоставимость показателей бюджетов бюджетной системы Российской Федерации."/>
      <Carousel/>
      <RightText bg="blue" svg={plan} title="ПЛАНИРОВАНИЕ БЮДЖЕТА" text={<><p>Решение о начале работы над  составлением  проекта  бюджета принимает Премьер Правительства Москвы   в   соответствии   с нормативными актами, определяющими  особенности  бюджетной  системы Российской Федерации в планируемом бюджетном году. <br/> Далее Правительство Москвы готовит проект бюджетного  послания  и представляет его Мэру Москвы. Бюджетное  послание  Мэра  Москвы  Московской  городской  Думе включает в себя: </p><ul><li>оценку  исполнения  бюджета   предшествующего и текущего финансовых годов; </li><li>прогноз   основных   показателей    социально-экономического положения Москвы в соответствующий период; </li><li>основные приоритеты бюджетной политики Москвы в  наступающем финансовом году; </li><li>сведения о прогнозируемых доходах бюджета Москвы; </li><li>проект бюджета Москвы.</li></ul></>}/>
      <TextGraph bg = "white" data = { [3746.9, 4183.1, -436.3] } label = { ["Доходы", "Расходы", "Дефицит/Профицит"] } title="ОСНОВНЫЕ ПАРАМЕТРЫ БЮДЖЕТА" text={ <> <b>Доход</b> - денежные средства, поступающие в бюджет от населения, организаций, учреждений и других бюджетов, за исключением средств, являющихся источниками финансирования дефицита бюджета. (К доходам бюджетов относятся налоговые доходы, неналоговые доходы и безвозмездные поступления) <br/><br/> <b>Расходы</b>  - выплачиваемые из бюджета денежные средства, за исключением средств, являющихся источниками финансирования дефицита бюджета. Бюджетные средства расходуются в различных направлениях: здравоохранение, транспорт, образование, социальная поддержка населения и прочее. <br/> <br/> В случае превышения доходов над расходами образуется <b>профицит</b>  бюджета. Если расходная часть бюджета превышает доходную возникает <b>дефицит</b>  бюджета. В таком случае возникает необходимость в привлечении источников финансирования дефицита бюджета.</>}/>
      <TextGraph bg = "blue" data = { [1716.8, 1213.0, 210.7, 341.8, 264.5, 0.5] } label = { ["Налог на доходы физических лиц", "Налог на прибыль организаций", "Налог, с упрощенной системы налогооблажений", "Прочие налоговые доходы", "Неналоговые доходы", "Безвозмездные поступления"] } title="ДОХОДЫ БЮДЖЕТА" text={ <> СОСТОЯТ ИЗ НАЛОГОВЫХ, НЕНАЛОГОВЫХ ДОХОДОВ И БЕЗВОЗМЕЗДНЫХ ПОСТУПЛЕНИЙ.<br/><br/>Налоговые доходы бюджета города Москвы формируются из федеральных, региональных и местных налогов, а также доходов от применения специальных налоговых режимов.</>}/>
      <TextGraph bg = "white" data = { [914.9, 635.2, 530.4, 521.4, 313.1, 823.3, 444.8] } label = { ["Развитие транспортной системы", "Социальная поддержка жителей города москвы", "Развитие здравоохранения города Москвы", "Развитие образование города москвы", "Жилище", "Прочие программы", "Непрограммные расходы"] } title = "РАСХОДЫ БЮДЖЕТА" text = { <> НАПРАВЛЕНЫ НА ФИНАНСОВОЕ ОБЕСПЕЧЕНИЕ ГОСУДАРСТВЕННЫХ ПРОГРАММ ГОРОДА МОСКВЫ<br/><br/><b>Программные расходы</b> – бюджетные средства, направленные на финансовое обеспечение государственных программ.<br/><br/><b>Государственная программа</b> – это документ, определяющий:<br/>- цели и задачи государственной политики в определенной сфере;<br/>- способы их достижения;<br/>- объемы используемых финансовых ресурсов;<br/>- координаторов и участников государственной программы;<br/>- непосредственные/ конечные результаты.</> } />
    
    </>
  );
}

export default Main;
