import React from "react";
import Graph from "./Graph";

export default function TextGraph(props) {
    let style = "TextGraph " + props.bg;
    let label = props.label;
    let data = props.data;

    return (
        <div className={ style } id={props.id}>
            <div className="text">
                <h1>{ props.title}</h1>
                <p>{ props.text }</p>
            </div>
            <div>
                <Graph data = { data } label = { label }/>
            </div>
        </div>
    )
}

